body {
	background-color: whitesmoke;
}
a,
Link {
	padding-left: 20px;
	text-decoration: none;
	color: dodgerblue;
}

a:hover {
	text-decoration: underline;
	color: rgb(51, 99, 230);
}

.nav h1 {
	margin: 0;
	padding: 20px;
	color: turquoise;
	background-color: #fff;
	width: fit-content;
	/* height: 70px; */
}

.todo-list,
.form {
	background-color: white;
	margin: auto;
	width: 80%;
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid #efefef;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), 0 10px 0 -5px #eee, 0 10px 1px -4px rgba(0, 0, 0, 0.15),
		0 20px 0 -10px #eee, 0 20px 1px -9px rgba(0, 0, 0, 0.15);
	padding: 30px;
	padding-bottom: 80px;
	margin-top: 0px;
}
.form {
	margin-top: 40px;
	box-shadow: none;
	width: 60%;
}

.heading {
	width: 82%;
	display: flex;
}

.heading p:nth-child(1) {
	width: 2%;
}

.heading p:nth-child(2) {
	width: 60%;
	margin-left: 2%;
}

.heading p:nth-child(3) {
	width: 10%;
	margin-left: 2%;
}

.heading p:nth-child(4) {
	width: 8%;
	margin-left: 2%;
}

.todo-list h2 {
	text-decoration: underline;
	font-size: 25px;
}
.todo-item {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 30px 30px 0;
	width: 90%;
	border-bottom: 1px solid #cecece;
	font-family: Roboto, sans-serif;
	font-weight: 100;
	font-size: 20px;
	color: #333333;
}
.todo-item input {
	margin-left: 0;
	width: 2%;
}

.todo-item span:nth-child(2) {
	width: 80%;
	margin-left: 2%;
	display: flex;
}

.todo-item p:nth-child(1) {
	width: 66%;
}

.todo-item p:nth-child(2) {
	width: 22%;
	margin-left: 2%;
}

.todo-item p:nth-child(3) {
	width: 8%;
	margin-left: 2%;
}

.todo-item > span:nth-child(3) {
	width: 6%;
	margin-left: 1%;
}
.todo-item > span:nth-child(4) {
	width: 8%;
	margin-left: 1%;
}

.LOW {
	color: rgb(46, 78, 104);
}
.MEDIUM {
	color: yellowgreen;
}
.HIGH {
	color: red;
}

input[type=checkbox] {
	margin-right: 10px;
	font-size: 30px;
}

input[type=checkbox]:focus {
	outline: 0;
}

form {
	width: 70%;
}

label {
	padding-right: 10px;
}

input[type=text] {
	height: 30px;
	width: 100%;
	margin-bottom: 20px;
	font-size: 15px;
	border: 1px solid rgb(0, 0, 0);
	border-radius: 3px;
	background-color: rgb(92, 150, 238);
	color: rgb(255, 255, 255);
	padding: 5px;
	outline: 0;
}
input[type=text]:focus {
	border: 2px solid rgb(0, 0, 0);
}
button {
	cursor: pointer;
	height: 40px;
	width: 100px;
	border: none;
	font-size: 15px;
	border-radius: 3px;
	color: #efefef;
	outline: 0;
}

.btn:hover {
	background-color: rgb(51, 99, 230);
}

.btn {
	margin-right: auto;
	background-color: dodgerblue;
}

.btn-delete {
	background-color: red;
}
.btn-delete:hover {
	background-color: darkred;
}
